
import Group from 'vux/src/components/group/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV3Data from 'vux/src/datas/china_address_v3.json'
import Icon from 'vux/src/components/icon/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import city from '@/components/SelectCity'
import {exchange} from '../config'
import {mapGetters} from 'vuex'
export default {
  name: 'goods',
  head: {
    title: {
      inner: '商品详情'
    }
  },
  components: {
    Group,
    XImg,
    XButton,
    XDialog,
    XInput,
    XAddress,
    ChinaAddressV3Data,
    Icon,
    Toast,
    city
  },
  data () {
    return {
      loading: false,
      id: 0,
      order: false,
      tips: false,
      address: ChinaAddressV3Data,
      toast: false,
      place: [],
      goods: {},
      form: {
        userId: '',
        productId: '',
        consignee: '',
        phone: '',
        address: ''
      }
    }
  },
  computed: {
    ...mapGetters({
      area: 'getInsuranceArea'
    })
  },
  created () {
    this.id = this.$route.params.id
    // 获取商品详情
    this.goods = JSON.parse(this.$localStorage.get('goods'))
  },
  methods: {
    handleJump (url) {
      this.tips = false
      this.$router.push(url)
    },
    handleOrder () {
      if (this.$localStorage.get('logined') === 'false') {
        this.$vux.toast.show({
          type: 'text',
          width: '20em',
          position: 'bottom',
          text: '当前未登录，无法进行兑换！',
          time: '1000'
        })
      } else {
        this.order = true
      }
    },
    handleSubmit () {
      const reg = /[\u4E00-\u9FA5\uF900-\uFA2D]/
      this.form.userId = JSON.parse(this.$localStorage.get('userInfo')).userId
      this.form.productId = this.$route.params.id
      if (!this.$refs.name.valid || !this.form.consignee || !reg.test(this.form.consignee)) {
        this.$vux.toast.show({
          type: 'text',
          width: '15em',
          position: 'bottom',
          text: '请填写正确的收货人姓名！',
          time: '1000'
        })
      } else if (!this.$refs.tel.valid || !this.form.phone) {
        this.$vux.toast.show({
          type: 'text',
          width: '15em',
          position: 'bottom',
          text: '请填写正确的手机号码！',
          time: '1000'
        })
      } else if (!this.area || !this.form.address) {
        this.$vux.toast.show({
          type: 'text',
          width: '15em',
          position: 'bottom',
          text: '请填写收货地址！',
          time: '1000'
        })
      } else {
        this.form.address = this.area + ',' + this.form.address
        this.$http({
          method: 'jsonp',
          url: exchange,
          jsonp: 'callback',
          jsonpCallback: 'json',
          params: this.form,
          before: () => {
            this.loading = true
            this.order = false
          }
        })
        .then(res => {
          this.loading = false
          if (res.body.status) {
            this.tips = true
          } else {
            this.$vux.toast.show({
              type: 'text',
              width: '15em',
              position: 'bottom',
              text: res.body.msg,
              time: '1000'
            })
          }
        })
      }
    }
  }
}



